import React, { memo } from 'react';
import { isEmpty, sortBy, groupBy, map, get, find } from 'lodash';
import { compose } from 'recompose';
import { withStyles } from '@material-ui/core/styles';
import { withRouter } from 'react-router';
import { Typography, Grid, TextField } from '@material-ui/core';
import Autocomplete, { createFilterOptions } from '@material-ui/lab/Autocomplete';
import PropTypes from 'prop-types';

import Loading from '../../../components/Loading';
import SERVICE_TYPES from '../service-types';
import ServiceService from '../../../services/service';
import withRequest from '../../../utils/with-request';
import ServiceOnDemandCard from './ServiceOnDemandCard';
import styles from './styles';

const defaultFilterOptions = createFilterOptions();

const OnDemand = memo(({ history, classes, servicesData, servicesLoading }) => {
  if (servicesLoading) return <Loading />;

  const serviceList = get(servicesData, 'services', []);
  const groupedServices = groupBy(serviceList, 'category');
  const askAnything = find(serviceList, s => s.title.match(/qualquer coisa/i));
  return (
    <React.Fragment>
      <Autocomplete
        options={sortBy(serviceList, 'title')}
        getOptionLabel={o => get(o, 'title', '')}
        onChange={(_, s) => {
          if (s) {
            history.push(`/servicos/${s.id}/solicitar-servico`);
          }
        }}
        filterOptions={(options, state) => {
          const results = defaultFilterOptions(options, state);
          if (isEmpty(results)) {
            return [askAnything];
          }
          return results;
        }}
        style={{ width: 320 }}
        renderInput={p => <TextField {...p} label="Qual servio procura?" variant="outlined" />}
      />
      {map(groupedServices, (services, categoryName) => (
        <div className={classes.categoryContainer} key={categoryName}>
          <Typography className={classes.categoryName} variant="h6">
            <b>{categoryName}</b>
          </Typography>
          <Grid container spacing={2}>
            {
              map(services, service => (
                <Grid key={service.id} item xs={12} sm={4} md={3}>
                  <ServiceOnDemandCard service={service} />
                </Grid>
              ))
            }
          </Grid>
        </div>
      ))}
    </React.Fragment>
  );
});

OnDemand.propTypes = {
  classes: PropTypes.object,
  history: PropTypes.object,
  servicesData: PropTypes.object,
  servicesLoading: PropTypes.bool
};

const withServices = withRequest(ServiceService.getServices, {
  name: 'services',
  options: ({ setAlert }) => ({
    variables: { serviceType: SERVICE_TYPES.ON_DEMAND },
    onRequestError: () => {
      setAlert({
        alertMsg: 'Erro ao carregar lista de servios',
        alertType: 'error',
        showAlert: true
      });
    }
  })
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withRouter,
  withServices
)(OnDemand);
